$emBase: 16px; //default
$accent_color: #328fff; //#ff6132; //#0984e3; //  #FF6132
$base_color: #fff;
$dark_base: #1e1e1e;

$border_color: #333;

$black: #f4f4f4;
$white: #322f2f;

$black_border: 2px solid $black;
$white_border: 2px solid $white;
$plyr-color-main: $accent_color;

@import '0-mixins';
@import '1-typography';
@import '2-grid';
@import 'plyr/src/sass/plyr.scss';
*,
html {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
    position: relative;
    height: 100%;
    min-height: 100vh;
    font-size: 1em;
    max-width: 100vw;
    overflow-x: hidden;
}

body.fixed {
    max-height: 100%;
    overflow-y: hidden;
}
.site {
    overflow-x: hidden;
}
.site .wrapper {
    display: block;

    @include breakpoint(phone) {
        width: 90%;
    }
    @include breakpoint(tablet) {
        width: 90%;
    }
    @include breakpoint(laptop) {
        width: 1000px;
    }
    @include breakpoint(desktop) {
        width: 1280px;
    }

    margin: 0 auto;
    min-height: 100%;
}

@import '3-nav';
@import '4-accessibility';
@import '5-forms';
@import '6-content';
@import 'fluidbox';
@import '7-media';
@import '8-switch';
@import '9-mode';
