
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000; /* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}
body {
	#loader	{ display: none; }
}
body.no-scroll {
	display: block;
	max-height: 100vh;
	pointer-events: none;
	overflow: hidden;

	#loader {
		display: block;

		div.overlay {
			position: absolute;
			display: block;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			width: 100%;
			height: 100%;
			z-index: 9998;
		}
		svg.spinner {
		  position: absolute;
		  top: 50vh;
		  left: 50%;
		  margin-left: -32px;
		  margin-top: -32px;
		  width: 64px;
		  height: 64px;
			z-index: 9999;

			.path {
			  fill: none;
			  stroke-width: 16px;
			  stroke-miterlimit: 10;
			  stroke-dasharray: 1500;
			  stroke-dashoffset: 1500;
			  stroke-linecap: square;
			  stroke-linejoin: bevel;
			  animation: dash .5s linear infinite;
			}
		}
	}
}
@keyframes dash {
  from { stroke-dashoffset: 5000; }
  to { stroke-dashoffset: 0; }
}
